@font-face {
  font-family: 'money';
  src: url('./fonts/VtksMoney2-2Bav.ttf') format('truetype');
}


.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  /* Ensure this layer is above the shader */
  /* Add more styling as needed */
}

.centered-content {
  position: relative;
  // width: 100vw;
  height: 100vh;

  .mainLogo {
    position: absolute;
    max-width: 80%; 
    max-height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  @media (min-width: 768px) {
    .mainLogo {
      max-width: 40%;
    }
  }

  .linkBar {
    position: absolute;
    max-width: 80%; 
    max-height: 100%;
    top: 80%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

a {
  text-decoration: none;
  color: black;

}


.appBar {
  min-height: 3em;
  background-color: black;
}



.backgroundGreen {
  background-size: cover;
}

.vibrate-1 {
  -webkit-animation: vibrate-1 0.3s linear infinite both;
  animation: vibrate-1 0.3s linear infinite both;
}

